
function SearchUser () {
    return (
        <div className='container d-flex row gx-4 border-bottom border-curve-10 border-success h-10-percent'>
            <div className='col row'>
                <input className='input-prompt border-curve-10' id='message' type='text'></input>
            </div>
            <div className='col-1'>
                <button className='btn btn-primary border-curve-10'>+</button>
            </div>
        </div>
    )
}


export default SearchUser